<template>
  <div class="goodlistes">
    <van-search
      v-model="value"
      shape="round"
      background="#e93323"
      placeholder="搜索商品信息"
    />
  </div>
</template>
<script>
export default {
  props: ["id"],
  data() {
    return {
      value: "",
      classification: [],
    };
  },
  created() {
    this.$axios
      .get(
        "/api/products?page=1&limit=8&keyword=&sid=" +
          this.$route.query.itemID +
          "&news=0&priceOrder=&salesOrder="
      )
      .then((d) => {
        this.classification = d.data.data;
        console.log(d.data.data);
      });
  },
};
</script>